@font-face {
    font-family: 'Embroidery';
    src: local('Embroidery'), url(/silvia-peligro/static/media/Embroidery.16dc9400.ttf) format('truetype');
}

@font-face {
    font-family: 'Spot-Matrix';
    src: local('Spot-Matrix'), url(/silvia-peligro/static/media/Spot-Matrix.68a4c297.ttf) format('truetype');
}
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1em}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body::-webkit-scrollbar{display:none}body{-ms-overflow-style:none}.content-wrap{width:100vw;display:flex;flex-wrap:wrap}

.main-nav{width:100vw;height:12vh;min-height:72px;padding-top:.3rem;position:fixed;z-index:101;top:0;left:0;background:white}.main-nav__menu_wrapper{width:100%;height:100%;display:flex;align-items:center;justify-content:space-between;padding:0 1rem;box-sizing:border-box}.main-nav__logo{height:10vh;min-height:58px}.main-nav__logo img{height:100%}.main-nav__hamburger{display:block;width:26px;height:16px;position:relative;z-index:102}.main-nav__hamburger i{-webkit-transition:.5s;transition:.5s;display:block;position:absolute;width:26px;height:2px;background-color:#161616;border-radius:5px}.main-nav__hamburger i:nth-of-type(1){-webkit-transition:.5s;transition:.5s;top:16px}.main-nav .nav-collapse{background:#161616;position:fixed;top:0;right:-80vw;width:80vw;height:100vh;overflow:auto;-webkit-transition:right .5s;transition:right .5s;padding-top:90px;padding-left:17%;z-index:101}.main-nav .nav-collapse__list{list-style:none;padding:0;margin:0}.main-nav .nav-collapse__item{margin-bottom:2rem}.main-nav .nav-collapse__item .dropdown-toggle{padding:0}.main-nav .nav-collapse__item .dropdown-item{color:white;outline:none}.main-nav .nav-collapse__item .dropdown-item:hover{background:transparent}.main-nav .nav-collapse__item .dropdown-menu{background:#161616;position:relative;width:100%}.main-nav .nav-collapse__item .dropdown-divider{opacity:.5}.main-nav .nav-collapse__link{color:white}.main-nav .nav-collapse__icons{margin-bottom:1.5rem;align-self:center}.main-nav .nav-collapse__icons img{width:2.3rem;margin-right:0.8rem}.showMenu .main-nav__hamburger i{-webkit-transform:rotate(-45deg);transform:rotate(-45deg);top:8px;background-color:white}.showMenu .main-nav__hamburger i:nth-of-type(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);top:8px}.showMenu .nav-collapse{right:0}@media only screen and (min-width: 30em){.main-nav .nav-collapse{width:70vw;padding-left:15%}}@media only screen and (min-width: 48em){.main-nav .nav-collapse{width:50vw;padding-left:11%}}@media only screen and (min-width: 62em){.main-nav__hamburger{display:none}.main-nav__menu_wrapper{padding:0 2rem}.main-nav .nav-collapse{display:flex;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:12vh;overflow:unset;min-height:72px;padding:0;right:2rem;background:white}.main-nav .nav-collapse__list{color:black;display:flex;justify-content:space-between;align-items:flex-end}.main-nav .nav-collapse__item{margin-bottom:0;margin-right:2rem}.main-nav .nav-collapse__item .dropdown-item{color:black}.main-nav .nav-collapse__item .dropdown-menu{position:absolute;background:white}.main-nav .nav-collapse__item .dropdown-divider{opacity:1}.main-nav .nav-collapse #home{display:none}.main-nav .nav-collapse__link{color:black}.main-nav .nav-collapse__icons{display:none}}.jumbotron{height:50vh}@media only screen and (min-width: 75em){.main-nav .nav-collapse__item{margin-right:3rem}}

.lang-select{margin:0;padding:5px;box-sizing:border-box;line-height:1.5;color:#495057;background:#fff;border-radius:0.25rem;border:1px solid #ced4da;-webkit-transition:border-color 3s ease-in-out, box-shadow 3s ease-in-out;transition:border-color 3s ease-in-out, box-shadow 3s ease-in-out}.lang-select:focus:not(.no-border),.lang-select:hover:not(.no-border){color:#495057;background:#fff;outline:0;border-color:#80bdff;box-shadow:0 0 0 0.2rem rgba(0,123,255,0.25)}

.mainSlideBanner{width:100%;height:100%;padding-bottom:3rem}.mainSlideBanner__boxImg{height:42vh;min-height:268px;background-size:cover;background-repeat:no-repeat;background-position:center}.mainSlideBanner__txtBox{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:.8rem}.mainSlideBanner__txtBox .title{margin:0;font-size:1.5rem;line-height:1.5rem}.mainSlideBanner__txtBox .paragraph{font-size:0.8rem;line-height:1.1rem;margin:.2rem 0 .5rem 0}@media only screen and (min-width: 48em){.mainSlideBanner__boxImg{height:63vh;min-height:350px}.mainSlideBanner__txtBox{position:absolute;bottom:3rem;max-height:42%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:.8rem .8rem 1.2rem;min-width:48vw;max-width:51vw;box-shadow:0 0 0 200px rgba(255,255,255,0.5) inset}.mainSlideBanner__txtBox .line-clamp{display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden;padding-bottom:0.2rem}}

.btn-p{padding:.3rem 2.5rem;font-size:0.8rem;border:none;border-radius:3px;background:#AC90F1;color:white}

.follow-me{width:100%;font-size:1.1rem;margin-bottom:0;text-align:center}.instagram-feed{margin:1rem auto;display:grid;grid-template-columns:repeat(2, 1fr)}.instagram-feed .post{min-width:160px;background-size:cover;background-position:center center;background-repeat:no-repeat}.instagram-feed .post:after{content:"";display:block;padding-bottom:100%}.instagram-feed .post:nth-last-child(-n+2){display:none}@media (min-width: 30em){.instagram-feed{grid-template-columns:repeat(3, 1fr)}.instagram-feed .post:nth-last-child(2){display:block}}@media (min-width: 48em){.instagram-feed{grid-template-columns:repeat(4, 1fr)}.instagram-feed .post{min-width:170px}.instagram-feed .post:nth-last-child(3){display:none}}@media (min-width: 64em){.instagram-feed{grid-template-columns:repeat(5, 1fr)}.instagram-feed .post:nth-last-child(-n+3){display:block}}

.footer{grid-row:15 / auto}.footer__border{width:100vw}.footer__border span{display:inline-block;width:100%;overflow:hidden;white-space:nowrap;font-size:2rem;line-height:0.5;color:#AC90F1}.footer .top-border{font-family:Spot-Matrix}.footer .top-border span{font-size:4rem}.footer .middle-border{font-family:Embroidery}.footer__input-wrapper{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:87%;margin:0 auto 1rem}.footer__input-wrapper p{text-align:center;font-size:0.8rem}.footer__input-wrapper .custom-input{width:100%;border-radius:5%;margin:0 auto}.footer__input-wrapper .form-control{border-radius:35px 0 0 35px;border-color:#AC90F1;border-right:none;font-size:0.8rem}.footer__input-wrapper .btn{color:black;line-height:1;background:transparent;border-color:#AC90F1;border-left:none;border-radius:0 35px 35px 0 !important;font-size:0.8rem;font-weight:600}.footer__menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:95%;margin:0.5em auto}.footer__menu ul{list-style:none}.footer__menu ul li{margin-bottom:0.3em;color:#43026A;cursor:pointer}.footer__menu img{width:2.3em;margin-right:0.8em;cursor:pointer}.footer__copyrights{background:#43026A;height:8vh;min-height:50px;display:flex;justify-content:center;align-items:center}.footer__copyrights .date{color:white;font-size:0.7rem}@media only screen and (min-width: 37.5em){.footer__input-wrapper .custom-input{width:85%}}@media only screen and (min-width: 48em){.footer__input-wrapper .custom-input{width:55%}.footer__menu{justify-content:center;margin:1.2em auto}.footer__menu ul{width:100%;display:flex;justify-content:center;padding:0 5%}.footer__menu ul li{display:flex;align-items:center}.footer__menu ul li::after{content:'\25C6';color:#43026A;margin:0 1em}.footer__menu ul li:last-child:after{content:'';margin:0}.footer__menu img{margin:0 0.3em}}@media only screen and (min-width: 62em){.footer__input-wrapper .custom-input{width:41%}}

.img-wrapper{margin-top:14vh;height:50vh;width:100vw;background-position:center center;background-repeat:no-repeat;background-size:cover}.img-wrapper .message{color:white;text-shadow:1px 1px 2px black;font-size:2rem;font-weight:700;position:absolute;left:1rem}@media (min-width: 48em){.img-wrapper{height:80vh}.img-wrapper .message{bottom:40%}}

